//   HTTP-
import { GithubRepoModel } from "@store/models/repos";
import { CollectionT } from "@utils/collection";

export enum HTTPMethod {
  GET = "GET",
  POST = "POST",
}

//  
export type RequestParams<ReqT> = {
  method: HTTPMethod; //  , GET  POST
  endpoint: string; // API-endpoint,    
  headers: Record<string, string>; //    HTTP-
  params?: Record<string, number>;
  /**
   *    .
   * -  GET-    query-    endpoint
   * -  POST-     JSON      ( )
   */

  data: ReqT;
};

//  API
export type ApiResponse<SuccessT = any, ErrorT = any> =
  | {
      success: true;
      data: SuccessT;
    }
  | {
      success: false;
      data: ErrorT;
    };

//   ,        API
export interface IApiStore {
  readonly baseUrl: string;

  request<SuccessT, ErrorT = any, ReqT = {}>(
    params: RequestParams<ReqT>
  ): Promise<ApiResponse<CollectionT<number, GithubRepoModel>>>;
}
